export default {
  name: "SideBar",
  data() {
    return {
      link: "now",
      mini: false,
      left: true
    };
  },
  methods: {
    showMenu() {
      if (this.left) {
        this.mini = !this.mini;
      } else {
        this.left = !this.left;
      }
    }
  }
};
